  height: 67vh;
  overflow: auto;
}
.message {
  width: 100%;
  display: block;
  overflow: auto;
  margin-bottom: 10px;
}
.message .v-size--default {
  height: 60px !important;
}
.publish-message {
  display: block;
  float: right;
  border-radius: 5px !important;
  height: auto !important;

}
.receive-message {
  display: block;
  float: left;
  border-radius: 5px !important;
  height: auto !important;

}
.subscription-item {
  width: 100%;
  text-align: center;
  margin-bottom: 10px;
  text-transform: unset !important;
}
.subscription-item .v-btn__content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 100%;
}
.message .v-chip__content {
  text-align: left;
}
.subscription-item .v-btn__content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 100%;
}
.border-left {
  border-left: 1px solid;
  border-width: 4px !important;
}
.active {
  background-color: #666666 !important;
}
