  position: relative;
}
.flex-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.app-status-chip-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.app-detail-switch-wrapper {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
}
.app-detail-switch-wrapper .caption {
  position: absolute;
  top: 0;
  right: 6px;
}
